.mce-content-body [data-shortcode="glossary_term"] {
  text-decoration: underline dotted grey;
  text-underline-offset: 2px;
}

.mce-content-body [data-shortcode="glossary_term"]:hover {
  background-color: yellow;
}

.inline-glossary-term {
  text-decoration: underline solid grey;
  text-underline-offset: 2px;
}

.inline-glossary-term:hover {
  background-color: grey;
  cursor: pointer;
}

.inline-glossary-definition {
  display: none;
}

.inline-glossary-term:hover .inline-glossary-definition {
  display: block;
  position: fixed;
  padding: 15px;
  margin-top: 5px;
  max-width: 250px;
  background-color: white;
  -webkit-box-shadow: 0px 2px 7px 0px rgba(179, 179, 179, 0.5);
  -moz-box-shadow: 0px 2px 7px 0px rgba(179, 179, 179, 0.5);
  box-shadow: 0px 2px 7px 0px rgba(179, 179, 179, 0.5);
}




